.login-container {
  background-color: #ffffff;
  width: 400px;
  height: 400px;
  margin: 7em auto;
  border-radius: 1.5em;
  box-shadow: 0px 11px 35px 2px rgba(0, 0, 0, 0.14);
}

.login-title {
  padding-top: 40px;
  color: #8c55aa;
  font-family: 'Ubuntu', sans-serif;
  font-weight: bold;
  font-size: 23px;
}

.login-form {
  padding-top: 40px;
}

.username-input {
  width: 76%;
  color: rgb(38, 50, 56);
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 1px;
  background: rgba(136, 126, 126, 0.04);
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  outline: none;
  box-sizing: border-box;
  border: 2px solid rgba(0, 0, 0, 0.02);
  margin-bottom: 50px;
  margin-left: 46px;
  text-align: center;
  margin-bottom: 0;
  font-family: 'Ubuntu', sans-serif;
}

.username-input:focus {
  border: 2px solid rgba(0, 0, 0, 0.18) !important;
}

.submit {
  cursor: pointer;
  border-radius: 5em;
  color: #fff;
  background: linear-gradient(to right, #9c27b0, #e040fb);
  border: 0;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 10px;
  padding-top: 10px;
  font-family: 'Ubuntu', sans-serif;
  margin-left: 35%;
  font-size: 13px;
  box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.04);
}

.submit:hover {
  text-decoration: none;
  background: linear-gradient(to right, #741b84, #b00bcc);
  color: #fff;
}

.help-text {
  color: rgb(38, 50, 56);
  width: 76%;
  font-size: 11px;
  margin: 0 auto;
  margin-bottom: 50px;
  font-style: italic;
}

.login-image {
  width: 80px;
  height: 80px;
  margin-left: 160px;
}

.incorrect-username {
  border: 2px solid #dc35459e;
}
